body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input-comp:focus{
    outline: none;
}


.button-comp:hover{
    background: rgba(255, 204, 51) !important;
    box-shadow: 0px 4px rgb(171, 141, 51) !important;
}

.button-comp:active{
    top: 4px !important;
    box-shadow: 0px 0px rgba(171, 141, 51, 0.74)  !important;
    outline: none !important;
}

.button-comp:focus{
    outline: none;
}
